import React from "react"
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';

const TableResults = ({results}) => {
  const useStyles = makeStyles({
    root: {
      width: '100%',
    },
    table: {
      minWidth: 500,
    },
    tableWrapper: {
      overflowX: 'auto',
    },
  });

  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
  <Paper className={classes.root}>
      <div className={classes.tableWrapper}>
          <Table className={classes.table} aria-label="simple table">
              <TableHead>
                  <TableRow>
                      <TableCell>Nom</TableCell>
                      <TableCell align="right">Lien GitHub</TableCell>
                      <TableCell align="right">Catgories</TableCell>
                      <TableCell align="right">Dure</TableCell>
                      <TableCell align="right">Participants</TableCell>
                      <TableCell align="right">Langue</TableCell>
                      <TableCell align="right">Kit  tlcharger</TableCell>
                  </TableRow>
              </TableHead>
              <TableBody>
              {(rowsPerPage > 0
                ? results.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                : results
              ).map(row => (
                  <TableRow key={row.name}>
                      <TableCell component="th" scope="row">
                          {row.name}
                      </TableCell>
                      <TableCell align="right"><a href={row.link} title={row.link}>Homepage</a></TableCell>
                      <TableCell align="right">{row.tags}</TableCell>
                      <TableCell align="right">{row.duration}</TableCell>
                      <TableCell align="right">{row.people}</TableCell>
                      <TableCell align="right">{row.language}</TableCell>
                      <TableCell align="right"><a href={row.kit} title={row.kit}>Lien</a></TableCell>
                  </TableRow>
              ))}

              {emptyRows > 0 && (
                  <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                  </TableRow>
              )}
              </TableBody>
              <TableFooter>
                  <TableRow>
                      <TablePagination
                        rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                        colSpan={3}
                        count={rows.length}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        SelectProps={{
                          inputProps: { 'aria-label': 'rows per page' },
                          native: true,
                        }}
                        onChangePage={handleChangePage}
                        onChangeRowsPerPage={handleChangeRowsPerPage}
                        ActionsComponent={TablePaginationActions}
                      />
                  </TableRow>
              </TableFooter>
          </Table>
      </div>
</Paper>)
}
export default TableResults
